@media screen and (min-width: 576px) {
  .secondTab .swiper-slide picture,
.secondTab .swiper-slide img,
.secondTab .swiper-slide .text {
    max-width: none;
    width: 100%;
  }
  .secondTab .swiper-slide {
    max-width: 400px;
    width: 400px;
  }
}
@media screen and (min-width: 768px) {
  .secondTab .swiper-button-next, .secondTab .swiper-button-prev {
    display: flex;
  }
  .sectionHeader_phone {
    display: block;
  }
  .sectionHeader_phone-mobView {
    display: none;
  }
  .tab_title {
    font-size: 4rem;
    text-align: center;
  }
  .tabsLabel {
    font-size: 2.25rem;
  }
  .sectionInner {
    width: 100%;
    max-width: 1397px;
    margin: 0 auto;
  }
  .firstTab_cards-item-title {
    font-size: 3rem;
  }
  .firstTab_cards-item-paragraph {
    font-size: 1.125rem;
  }
  .firstTab_cards-item {
    display: flex;
    flex-direction: row;
    align-items: flex-start;
    justify-content: flex-start;
  }
  .firstTab_cards-item:nth-child(2n) {
    flex-direction: row-reverse;
  }
  .firstTab_cards-item:nth-child(2n) .firstTab_cards-item-textContent {
    margin: 0 0 0 200px;
  }
  .firstTab_cards-item-textContent {
    margin: 0 200px 0 0;
  }
  .firstTab_cards-item-img {
    flex-shrink: 0;
  }
}
.fancybox__nav .carousel__button.is-prev {
  right: 88px;
}